import React, {useState, useEffect} from 'react';
import ArticlesList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';
import articleContent from './article-content';
import CommentsList from '../components/CommentsList';
import ArticleMainImage from '../components/ArticleMainImage';
import ArticleParagraph from '../components/ArticleParagraph';

function isParagraph(key, props){
    const contentKey = props.contentKey;
    const paragraphContent = props.paragraphContent;

    if(paragraphContent != null){
        return <ArticleParagraph keyId={key} paragraphContent={props.paragraph }/>
    }
}

const ArticlePage = ({ match }) => {
        const name = match.params.name;
        const article = articleContent.find(article => article.name === name); 

        const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments: [{username: "MadB055", text:"This is the BEst zBlog Ever!"}]});
        let firebaseData = {};
        useEffect(() => {
            const fetchData = async () =>{
                const result = await fetch(`/api/articles/${name}`);
                const body = await result.json();
                console.log(body);
                setArticleInfo(body);
                firebaseData = body;
            }
            fetchData();
            setArticleInfo({ upvotes: 3});
            setArticleInfo({comments: [{username: "MadB055", text:"This is the BEst zBlog Ever!"},{username: "MadB055", text:"This is the BEst zBlog Ever!"}]})
        },[name]);

        if(!article){
            return <NotFoundPage />
        }

        const otherArticles = articleContent.filter(article => article.name !== name);

        return(
            <>
            <div id="page-body">
            <h1 className="hand_written_header">{article.title}</h1>
            {/* {articleInfo.upvotes} */}
            <ArticleMainImage articleInfo={articleInfo}/>
        <p>This post has been upvoted {articleInfo.upvotes}  times</p>
            <p>{articleInfo.intro_paragraph}</p>
            
            {article.content.map((paragraph, key) =>
                <ArticleParagraph keyId={key} paragraphContent={paragraph}/>
            )}
            {console.log("Static "+article.content)}
            {console.log("test1 "+JSON.stringify(articleInfo))}
            //Testing Content from Firebase
            {console.log("firebase "+articleInfo.article_body)}
            {console.log("test2: "+firebaseData)}
             {/* {articleInfo.article_body.map((articleContentChunk, key) =>
                <isParagraph contentKey={key}  paragraphContent={articleContentChunk.paragraph}/>
            )}   */}
            

            <CommentsList comments={articleInfo.comments} />

            <h3>Other Articles:</h3>
            <ArticlesList articles={otherArticles} /> 
            </div>
            </>
        );
    }

export default ArticlePage;